import './App.css';
import TrangChinh from './pages/TrangChinh/TrangChinh'
import React from 'react'
import ThongTin from './pages/ThongTin/ThongTin'
function App() {
  const [Page,SetPage] = React.useState('TrangChinh')
  const _Trang = (e)=>{
    SetPage(e)
  }
  console.log(Page)
  if(Page==='TrangChinh')
  {
    return (
      <div className="App">
          <TrangChinh Trang={_Trang}></TrangChinh>
      </div>
    );
  }
  else if(Page==='ThongTin'){
    return (
      <div className="App">
          <ThongTin Trang={_Trang}></ThongTin>
      </div>
    );
  }

}

export default App;
