import React ,{Fragment}from 'react'
import Header from '../Header'
import Body from './Body'
import Footer from '../Footer'
function TrangChinh({Trang}) {
    const _Trang = (e)=>{
        Trang(e)
    }
    return (
        <Fragment>
            <Header Trang={_Trang}></Header>
            <Body></Body>
            <Footer></Footer>
        </Fragment>
    )
}

export default TrangChinh
